// ** React Imports
import { useSelector } from "react-redux"
// ** Custom Components
import UserListAvatar from '@components/user-avatar/UserListAvatar'
import TableAction from '@components/table/TableAction'

// ** Store & Actions
import { getEmployee } from '@store/Employees/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Badge } from 'reactstrap'
import { Archive, Bell, FileText, Gitlab, Globe, Shield, Star } from 'react-feather'

import { formatPhone } from '@helper'

// ** Renders Role Columns
const renderRole = row => {
  const roleObj = {
    tech_admin: {
      class: 'text-danger',
      icon: Gitlab
    },
    admin: {
      class: 'text-primary',
      icon: Globe
    },
    operations: {
      class: 'text-primary',
      icon: Shield
    },
    agent: {
      class: 'text-primary',
      icon: Star
    }
  }
  const { rolesConfig } = useSelector(state => state.roles)
  const { roles } = rolesConfig.employee
  const Icon = roleObj[row.role] ? roleObj[row.role].icon : Bell

  return (
    <span className='text-truncate text-capitalize align-middle'>
      <Icon size={18} className={`${roleObj[row.role] ? roleObj[row.role].class : ''} mr-50`} />
      {roles[row.role]}
    </span>
  )
}

const statusObj = {
  pending: 'light-warning',
  active: 'light-success',
  deactivated: 'light-secondary',
  deleted: 'light-danger'
}

export const columns = [
  {
    name: 'Employee',
    minWidth: '297px',
    selector: 'fullName',
    sortable: true,
    cell: row => (
      <UserListAvatar
        name={`${row.firstName} ${row.lastName}`}
        photo={row.profilePictureURL}
        viewHandler={() => store.dispatch(getEmployee(row._id))}
        viewLink={`/employees/view/${row._id}`}
      />
    )
  },
  {
    name: 'Email',
    minWidth: '229px',
    selector: 'email',
    sortable: true,
    cell: row => row.email
  },
  {
    name: 'Phone Number',
    minWidth: '229px',
    selector: 'phoneNumber',
    sortable: true,
    cell: row => <span className='text-capitalize'>
      {formatPhone(row.phoneNumberStr)}
    </span>
  },
  {
    name: 'Role',
    minWidth: '172px',
    selector: 'role',
    sortable: true,
    cell: row => renderRole(row)
  },
  {
    name: 'Status',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: row => {
      const { rolesConfig } = useSelector(state => state.roles)
      const { status } = rolesConfig.employee
      return (
        <Badge className='text-capitalize' color={statusObj[row.status]} pill>
          {status[row.status]}
        </Badge>
      )
    }
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => {
      const actions = [
        {
          link: `/employees/view/${row._id}`,
          handler: () => store.dispatch(getEmployee(row._id)),
          label: 'Details',
          icon: (<FileText size={14} className='mr-50' />)
        },
        {
          link: `/employees/edit/${row._id}`,
          handler: () => store.dispatch(getEmployee(row._id)),
          label: 'Edit',
          icon: (<Archive size={14} className='mr-50' />)
        }
      ]
      return (
        <TableAction actions={actions} />
      )
    }
  }
]
