import { useState } from "react"
import { useSelector } from 'react-redux'

// ** Invoice List Sidebar
import Sidebar from './Sidebar'

// ** Columns
import { columns } from './columns'

// ** Store & Actions
import { getAllEmployees, getEmployeeList } from '@store/Employees/actions'

// ** Custom Components
import CarhootTable from '@components/table/CarhootTable'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import '@styles/react/apps/app-users.scss'

const EmployeesList = () => {
  // ** States
  const [currentRole, setCurrentRole] = useState({ value: '', label: 'Select Role' })
  const [currentStatus, setCurrentStatus] = useState({ value: '', label: 'Select Status', number: 0 })
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const { allData, data, total } = useSelector(state => state.employees)
  const { rolesConfig } = useSelector(state => state.roles)
  const { roles, status } = rolesConfig.employee

  const roleOptions = [{ value: '', label: 'Select Role' }]
  for (const rKey in roles) {
    roleOptions.push({ value: rKey, label: roles[rKey] })
  }
  const statusOptions = [{ value: '', label: 'Select Status' }]
  for (const sKey in status) {
    statusOptions.push({ value: sKey, label: status[sKey] })
  }

  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen)

  const customFilters = () => {
    return [
      {
        key: 'role',
        options: roleOptions,
        value: currentRole,
        onChange: setCurrentRole
      },
      {
        key: 'status',
        options: statusOptions,
        value: currentStatus,
        onChange: setCurrentStatus
      }
    ]
  }

  const customSearch = () => {
    return {
      role: currentRole.value,
      status: currentStatus.value
    }
  }

  return (
    <div className='app-user-list'>
      <CarhootTable
        allData={allData}
        data={data}
        total={total}
        columns={columns}
        filters={customFilters()}
        searchParams={customSearch()}
        getList={getEmployeeList}
        getAllList={getAllEmployees}
        headerActionHandler={toggleSidebar}
        headerActionLabel={'Add New Employee'}
      />
      <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
    </div>
  )
}

export default EmployeesList
